*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.commonteacherSalaries {
  display: flex;
  flex-direction: column;
  height: 97vh;
  align-items: center;
}
#date {
  margin-left: 5px;
}
.salariesTh {
  padding-left: 5px;
  padding-right: 5px;
}
.salariesTd {
  padding-left: 5px;
  padding-right: 5px;
}
.teacherSalariesTableTr {
  text-align: center;
}
.commonteacherSalariesTable {
  border: 2px solid black;
  width: 100vw;
  overflow: scroll;
}
.teacherSalariesTableTr,
.teacherSalariesTableTr th,
.teacherSalariesTableTr td {
  border: 2px solid white;
  padding: 5px;
  color: white;
}
.commonteacherSalariesTable tr:nth-child(even) {
  background-color: rgb(55, 15, 92);
}

.commonteacherSalariesTable tr:nth-child(odd) {
  background-color: rgb(55, 15, 92);
}
.padding_5 {
  padding: 5px;
}


.commonteacherSalaries input{
  border: 1px solid black;
  border-radius: 5px;
}
.pageMwnuBackground{
  background-color: #380a3a;
  width: 100%;
  color: white;
}