* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
  font-family: "Baloo 2", cursive;
}

input {
  outline: none;
  border: none;
}

button {
  outline: none !important;
  border: none;
  background: transparent;
}

button:hover {
  cursor: pointer;
}

/*-- contenedor del Login--*/

.container-login {
  width: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;  
  flex-wrap: wrap; 
  justify-content: center;
  align-items: center;
  padding: 15px;
  background: -webkit-linear-gradient(to right, rgb(17, 1, 54), #971a97);
  background: linear-gradient(to right, rgb(17, 1, 54), #971a97);
}   

.wrap-login {
  width: 400px;
  background: #eceff1;
  border-radius: 20px;
  overflow: hidden;
  padding: 77px 55px 53px 55px;
  /* -webkit-box-shadow: 25px 40px 28px 0px rgba(0, 0, 0, 0.38); */
}

/*----Formulario de user y password----*/

.login-form {
  width: 100%;
}

.login-form-title {
  display: block;
  font-family: "Baloo 2", cursive;
  font-size: 40px;
  color: #333333;
  line-height: 1.5;
  text-align: center;
}

.avatar {
  /*Centrar imagen*/

  display: block;
  margin: auto;
  width: 80px;
  height: 80px;
}

/*------------------------------------------------------------------ 
    
    [ Input ]*/

.wrap-input100 {
  width: 100%;
  position: relative;
  border-bottom: 2px solid #adadad;
  margin-bottom: 37px;
}

.input100 {
  font-family: "Baloo 2", cursive;
  font-size: 15px;
  color: #555555;
  line-height: 1.2;
  display: block;
  width: 100%;
  height: 45px;
  background: transparent;
  padding: 0 5px;
}

/*---------------------------------------------*/

.focus-efecto {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
}

.focus-efecto::before {
  content: "";
  display: block;
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 4px; /*ancho de la linea que hace el efecto de barra de progeso en el input al hacer foco*/
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
  background: rgb(75, 46, 141);
  background: -webkit-linear-gradient(left, rgb(17, 1, 54), #971a97);
  background: -o-linear-gradient(left, rgb(17, 1, 54), #971a97);
  background: -moz-linear-gradient(left, rgb(17, 1, 54), #971a97);
  background: linear-gradient(left, rgb(17, 1, 54), #971a97);
}

.focus-efecto::after {
  font-family: "Baloo 2", cursive;

  font-size: 15px;

  color: #999999;

  line-height: 1.2;

  content: attr(data-placeholder);

  display: block;

  width: 100%;

  position: absolute;

  top: 16px;

  left: 0px;

  padding-left: 5px;

  -webkit-transition: all 0.4s;

  -o-transition: all 0.4s;

  -moz-transition: all 0.4s;

  transition: all 0.4s;
}

.input100:focus + .focus-efecto::after {
  top: -15px;
}

.input100:focus + .focus-efecto::before {
  width: 100%;
}

.has-val.input100 + .focus-efecto::after {
  top: -15px;
}

.has-val.input100 + .focus-efecto::before {
  width: 100%;
}

/*---------------------------------------------*/

/*------------------------------------------------------------------ 
    
    [ Button ]*/

.container-login-form-btn {
  display: -webkit-box;

  display: -webkit-flex;

  display: -moz-box;

  display: -ms-flexbox;

  display: flex;

  flex-wrap: wrap;

  justify-content: center;

  padding-top: 30px;
}

.wrap-login-form-btn {
  width: 100%;

  display: block;

  position: relative;

  z-index: 1;

  border-radius: 40px 5px;

  overflow: hidden;

  margin: 0 auto;
}

.login-form-bgbtn {
  position: absolute;

  z-index: -1;

  width: 300%;

  height: 100%;

  background: #a64bf4;

  background: -webkit-linear-gradient(
    right,
    rgb(17, 1, 54),
    #971a97,
    rgb(17, 1, 54),
    #971a97
  );

  background: -o-linear-gradient(right, rgb(17, 1, 54), #971a97, rgb(17, 1, 54), #971a97);

  background: -moz-linear-gradient(right, rgb(17, 1, 54), #971a97, rgb(17, 1, 54), #971a97);

  background: linear-gradient(right, rgb(17, 1, 54), #971a97, rgb(17, 1, 54), #971a97);

  top: 0;

  left: -100%;

  -webkit-transition: all 0.4s;

  -o-transition: all 0.4s;

  -moz-transition: all 0.4s;

  transition: all 0.4s;
}

.login-form-btn {
  font-family: "Baloo 2", cursive;

  font-size: 20px;

  color: #fff;

  line-height: 1.2;

  text-transform: uppercase;

  display: -webkit-box;

  display: -webkit-flex;

  display: -moz-box;

  display: -ms-flexbox;

  display: flex;
  background: linear-gradient(to right, rgb(17, 1, 54), #971a97);

  justify-content: center;

  align-items: center;

  padding: 0 20px;

  width: 100%;

  height: 50px;
}

.wrap-login-form-btn:hover .login-form-bgbtn {
  left: 0;
}

nav.navbar a.navbar-brand {
  font-size: 30px;

  color: #fff;

  display: inline-block;

  font-family: "Baloo 2", cursive;

  padding: 0 20px;
}

a.navbar-brand::after {
  content: "";

  display: block;

  width: 0;

  height: 2px;

  background: #fff;

  transition: width 0.3s;
}

a.navbar-brand:hover::after {
  width: 100%;

  transition: width 0.3s;
}

img .circular {
  border-radius: 50%;
}

nav.navbar {
  /* -webkit-box-shadow: 25px 40px 28px 0px rgba(0, 0, 0, 0.38); */

  background: -webkit-linear-gradient(to right, rgb(17, 1, 54), #971a97);

  background: linear-gradient(to right, rgb(17, 1, 54), #971a97);

  border-radius: 0 0 10px 10px;
}

nav.navbar li.nav-item a.nav-link {
  font-size: 20px;

  color: #fff;

  display: inline-block;

  font-family: "Baloo 2", cursive;

  padding: 0 20px;
}

nav.navbar li.nav-item a.nav-link::after {
  content: "";

  display: block;

  width: 0;

  height: 2px;

  background: #fff;

  transition: width 0.3s;
}

nav.navbar li.nav-item a.nav-link:hover::after {
  width: 100%;

  transition: width 0.3s;
}

