*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.commonCashbox {
  display: flex;
  flex-direction: column;
  height: 97vh;
  align-items: center;
}
#startDate {
  margin-left: 5px;
}
#lastDate {
  margin-left: 5px;
}
.kassaTh {
  padding-left: 5px;
  padding-right: 5px;
}
.kassaTd {
  padding-left: 5px;
  padding-right: 5px;
}
.cashBoxTableTr {
  text-align: center;
}
.commonCashboxTable {
  border: 2px solid black;
  width: 100vw;
  overflow: scroll;
}
.cashBoxTableTr,
.cashBoxTableTr th,
.cashBoxTableTr td {
  border: 2px solid rgb(255, 255, 255);
  padding: 5px;
}
.cashBoxTableTr th{
  background-color: #A4CAFE;
  color: black;
}
.commonCashboxTable tr:nth-child(even) {
  background-color: #bdd9ff;
  color: black;
}

.commonCashboxTable tr:nth-child(odd) {
  background-color: #9ee6c9;
  color: black;
}




.padding_5 {
  padding: 5px;
}

.commonCashbox input{
  border: 1px solid black;
  border-radius: 5px;
}
.pageMwnuBackground{
  background-color: #380a3a;
  width: 100%;
  color: white;
  display: flex;
  justify-content: end;
  padding: 5px;
}