.columnName {
  background-color: rgb(24 78 63);
  padding: 10px 5px 10px 5px;
  color: white;
  white-space: nowrap;
}

.commonColumnName th {
  user-select: none !important;
  text-align: center;
}

.forTacherGroups li {
  list-style-type: none;
  padding: 5px;
  border: 1px solid rgb(122, 119, 119);
}

.columnValues td {
  padding-top: 3px;
  padding-bottom: 3px;
  text-align: center;
}

.columnValues {
  padding-left: 5px;
  padding-right: 5px;

}



.forTacherGroups {
  display: flex;
  align-items: flex-start;
  height: 90vh;
  width: 100%;
  overflow: scroll;
}
.forTacherGroups .contracts-table{
  width: 100%;
}

/*  */
/*  */
/*  */

.nextMonths {
  display: flex;
}

.previousMonths {
  display: flex;
}

.buttonStyle {
  border-radius: 5px;
  min-width: 40px;
}
.minds{
  min-width:150px;
}
.tableOfModal {
  display: flex;
  justify-content: center;
  text-align: center;
  max-height: 40vh;
  overflow: scroll;
}

/*  */
/*  */
/*  */
.contracts-table td{
  font-size: 13px !important;
}
 


.contracts-table th{
  font-size: 13px !important;
  padding: 3px !important;
}



.contracts-table th, .contracts-table td {
  border: 1px solid rgba(255, 255, 255, 0.822)
}

.padding_5 {
  padding: 5px;
}

.commonPaymentTr:nth-child(odd) {
  background-color: rgb(71, 18, 61);
  color: white;
}

.commonPaymentTr:nth-child(even) {
  color: black;
}

.submitBtn {
  background-color: rgb(71, 18, 61) !important;
  color: white !important;
}

.color-1 {
  background-color: #73d0f8;
}

.color-2 {
  background-color: #BCF0DA;
}

.not-full-paid {
  background-color: rgba(255, 24, 24, 0.801);
  color: black;
}

.will-pay {
  background-color: rgba(245, 230, 29, 0.849);
  color: black;
}

.already-paid {
  background-color: rgba(120, 224, 71, 0.849);
  color: black;
}

.not-debt {
  background-color: rgba(76, 92, 214, 0.849);
  color: black;
}


.not-exists {
  background-color: black;
  color: white;
}