.search_teaacher {
  padding: 5px;
  border: 2px solid black;
  border-radius: 5px;
  background-color: #664366;  
  color: white;
  text-align: center;
}
.search_teaacher::placeholder {
  color: white;
  opacity: 1;
}
.common_teachers_name{
  height: 85vh;
  overflow: scroll;
}
.common_teachers_name ul,
li,
p {
  text-align: center;
  padding: 5px;
  margin: 2px;
}
.common_teachers_name ul{
  padding: 5px 0px 5px 0px !important;
}
.common_teachers_name p {
  background-color: #380a3a;
  color: white;
  user-select: none;
}
.teachers_name ul{
  height: 100vh;
  scroll-behavior: smooth;
}
.teachers_name li{
  user-select: none;
}
.slidebarOpen {
  left: 0;
  transition: all 0.3s ease;
}
.slidebarClose {
  position: fixed;
  top: 0;
  left: -200px;
  width: 200px;
  height: 100vh;
  background-color: #fff;
  transition: all 0.3s ease;
}
.teacherNameBGColor {
  background-color: #ccd5e9;
}
.teachers_name .teacherNameBGColor:hover {
  background-color: rgb(24 78 63);
  color: white;
}
.activeTeacherName {
  color: white;
  background-color: #664366;
}
